import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { combineLatestWith, filter, Observable, startWith, take } from 'rxjs';
import { map } from 'rxjs/operators';
import { Store } from '@ngrx/store';
import { TranslateService } from '@ngx-translate/core';

import { ILoginInfo } from '@ApiClient/backend/Login/loginInfo';
import { IPostFull } from '@ApiClient/backend/Post/post';

import { selectIsLoadingProfile, selectIsLoadingProfileFeed, selectUserLoginInfo, selectUserPosts } from '@store/profile/profile.selectors';
import { getLoginInfoAction, getUserPostsAction } from '@store/profile/profile.actions';
import { AppStateInterface } from '@store/app/app-state.interface';
import { selectCanUpload, selectCurrentUserNickname } from '@store/auth/auth.selectors';
import { URLS } from '@enums/urls';

enum PROFILE_TABS {
  POSTS = 'posts',
  GALLERY = 'gallery',
  COLLECT = 'collections',
  ABOUT = 'about',
}

@Component({
  selector: 'app-user-profile',
  templateUrl: 'profile.component.html',
  styleUrls: ['profile.component.scss'],
})
export class ProfileComponent implements OnInit {
  locale$: Observable<string> = this.translate.onLangChange.pipe(
    startWith({ lang: this.translate.currentLang }),
    map((event) => event.lang),
  );
  count: number = 4;
  currentTab: PROFILE_TABS = PROFILE_TABS.POSTS;
  isCurrentUser: boolean = false;
  userPosts$: Observable<IPostFull[] | null> = this.store.select(selectUserPosts);
  isLoading$: Observable<boolean> = this.store.select(selectIsLoadingProfile);
  isLoadingFeed$: Observable<boolean> = this.store.select(selectIsLoadingProfileFeed);
  canUpload$: Observable<boolean> = this.store.select(selectCanUpload);
  userLoginInfo$: Observable<ILoginInfo | null> = this.store.select(selectUserLoginInfo);
  params: string = '';

  constructor(
    private store: Store<AppStateInterface>,
    private route: ActivatedRoute,
    private translate: TranslateService,
  ) {}

  async ngOnInit() {
    this.route.params.pipe(combineLatestWith(this.store.select(selectCurrentUserNickname))).subscribe(([params, currentUser]) => {
      this.isCurrentUser = params['userSlug'] === currentUser;
      this.store.dispatch(getLoginInfoAction({ nicknameUrl: params['userSlug'] }));
    });
  }

  onScrollingFinished() {
    this.isLoading$
      .pipe(
        combineLatestWith(this.userLoginInfo$),
        filter(([isLoading]) => !isLoading),
        take(1),
      )
      .subscribe(([, userInfo]) => {
        this.store.dispatch(getUserPostsAction({ count: this.count, userId: userInfo!.id }));
      });
  }

  setTab(newValue: PROFILE_TABS) {
    this.currentTab = newValue;
  }

  protected readonly URLS = URLS;
  protected readonly PROFILE_TABS = PROFILE_TABS;
}
